import React, { Component, memo } from "react";
import ReactPlayer from "react-player";
import { isSocialWall, isTagShop } from "../../../../actions/api";
import { GET_IMAGE_SIZE } from "../../../../utils/Common";

class Video extends Component {
    constructor(props) {
        super(props);
        this.state = {
            loadError: false,
            videoLoaded: false,
            calcHeight: 0,
            calcWidth: 0,
            currentHeight: 0,
            currentWidth: 0,
        };
        this.mediaRef = React.createRef();
        this._isMounted = false; // Track component mount status
    }

    componentDidMount() {
        this._isMounted = true;
        requestAnimationFrame(() => {
            this.updateMediaDimensions();
            this.mediaSizeCalc();
        });
    }

    componentWillUnmount() {
        this._isMounted = false;
    }

    updateMediaDimensions = () => {
        if (this.mediaRef.current) {
            const { offsetHeight, offsetWidth } = this.mediaRef.current;
            this.setState({
                currentHeight: offsetHeight,
                currentWidth: offsetWidth,
            });
        }
    };

    mediaSizeCalc = async () => {
        const { media } = this.props.Post;
        if (!media?.height && !media?.width) {
            try {
                const { width, height } = await GET_IMAGE_SIZE(media?.image?.small);
                if (this._isMounted) {
                    this.setState({
                        calcHeight: height,
                        calcWidth: width,
                    });
                }
            } catch (error) {
                if (this._isMounted) {
                    this.setState({
                        calcHeight: 300,
                        calcWidth: 300,
                    });
                }
            }
        }
    };

    setVideoLoaded = (event) => {
        if (this._isMounted) {
            this.setState({ videoLoaded: event });
        }
    };

    render() {
        const { VideoClass, Post, controls, autoPlay, handleVideoEnded, muted, intervalTime, size, inView } = this.props;
        const { calcHeight, calcWidth, currentWidth, currentHeight } = this.state;
        const { media, id, link, networkId } = Post;
        const fullsize = size === 1;
        const height = media?.height ? media?.height : calcHeight;
        const width = media?.width ? media?.width : calcWidth;
        const paddingSize = size ? size : (height * 100) / width;
        const videoStyle = {
            paddingBottom: `${fullsize ? null : paddingSize}%`,
        };

        return (
            <div className={`${VideoClass}_wrap_`} style={videoStyle} ref={this.mediaRef}>
                <ReactPlayer
                    key={`${id}-${currentHeight}-${calcWidth}-${inView}`}
                    className={`${VideoClass} tb_media-${id}`}
                    url={media?.video?.clip ? media?.video?.clip : inView ? media?.video?.full : media?.image?.small}
                    data-height={calcHeight}
                    data-width={calcWidth}
                    data-type="video"
                    data-network={networkId}
                    data-link={link}
                    data-item-id={id}
                    data-load={0}
                    config={{
                        facebook: {
                            appId: '117029975354072',
                            attributes: {
                                'data-width': width,
                                'data-height': height,
                                poster: media?.image?.small,
                            },
                        },
                        file: {
                            attributes: {
                                preload: 'metadata',
                                poster: media?.image?.small,
                                height: currentHeight,
                                width: currentWidth,
                            },
                        },
                    }}
                    loop={isSocialWall && !isTagShop && Post?.loopStop ? false : autoPlay}
                    onError={(e) => {
                        if (fullsize && handleVideoEnded) {
                            setTimeout(() => {
                                if (this._isMounted && handleVideoEnded) handleVideoEnded();
                            }, intervalTime * 1000);
                        }
                        this.setVideoLoaded(false);
                    }}
                    autoPlay={inView && autoPlay ? 1 : 0}
                    muted={muted}
                    volume={autoPlay ? 1 : 0}
                    playsinline={1}
                    onReady={() => this.setVideoLoaded(!isTagShop)}
                    playing={inView && autoPlay ? 1 : 0}
                    controls={controls || false}
                    onEnded={handleVideoEnded}
                    title={media?.title}
                />
            </div>
        );
    }
}

export default memo(Video);