import React, { memo } from "react"; 
import { timeAgo } from "../../../../utils/Common";
const Time = (props) => {
  const { createdAt, timeClass } = props;
  return <div className={timeClass} role="status" aria-label={`post timestamp ${timeAgo(createdAt)}`}>{timeAgo(createdAt)}</div>
}

export default memo(Time)