import React, { PureComponent } from "react";
class CTAButton extends PureComponent {
  render() {
    const { Post, ctaClass, onClickToCTA } = this.props;
    const {cta} = Post
    const ctaStyle = {
      color: cta?.color,
      backgroundColor: cta?.background
    }

    return <div className={ctaClass} style={ctaStyle} role="button" tabIndex="0" aria-label="CTA Button"
      onClick={
        (e) => {
          e.stopPropagation();
          onClickToCTA(Post)
          window.open(
            `${cta.url}`,
            '_blank')
        }}
    > {cta.text}</div>
  }
}


export default (CTAButton);