import React, { Suspense, lazy } from "react";
import { isInstaUser } from "../../../../utils";
import Seprator from "../Seprator/Seprator";
import Time from "../Time/Time";
import Author from "./Author";
const Network = lazy(() => import( "../Network/Network"));

const AuthorDetails = ({ className, Post, CardStyle, inView, size }) => {
       const { author, networkId } = Post
       const username = author.username ? author.username.length > 0 ? `@${author.username}` : `` : '';
       const instaUser = isInstaUser(author.name);
       const postAuthor = (CardStyle.author.status && !instaUser) ? true : false;
       const authorSeprator = (CardStyle.author.status && CardStyle.timeStatus && !instaUser && username != '') ? true : false;
       return <div className={`${className}_author_wrapper`} aria-label="Author details" aria-roledescription="post author details">
              <div className={`${className}_author`}>
                     {postAuthor ?
                     <Author author={author} network={Post.network} networkId={networkId} authorClass={`${className}_author_profile`} size={size} inView={inView} />
                     : ''}
                     <div className={`${className}_author_info`}>
                            {postAuthor ? <div className={`${className}_authorname`}>{author.name}</div> : ''}
                            <div className={`${className}_post_info`}>
                                   {postAuthor && username != '' ? <div className={`${className}_username`}>{username}</div> : ''}
                                   {authorSeprator ? <Seprator sepClass={`${className}_seprator`} /> : ''}
                                   {CardStyle.timeStatus ? <Time createdAt={Post.createdAt} timeClass={`${className}_time tb-cTBfont-${CardStyle.author.variant}`} /> : ''}
                            </div>
                     </div>
              </div>
              <div className={`${className}_social_`}>
                     <Suspense fallback={<div style={{ minWidth: 20 }}></div>}>
                            <Network networkClass={`${className}_social_ico`} networkId={networkId} iconStyle={CardStyle.icon} />
                     </Suspense>
              </div>
       </div>
}
export default AuthorDetails